import { pluginName } from './constants';

const CKEDITOR = window.CKEDITOR;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colors = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /*************************************************
     * Helper ‚Äì  clone the whole parentChain **inside** the
     * colour <span> so     <strong>txt</strong>  becomes
     * <span style='color'><strong>txt</strong></span>
     *************************************************/
    function wrapInside(node, parentChain) {
      return parentChain.reduceRight((inner, outer) => {
        const wrapper = new CKEDITOR.dom.element(outer.getName());
        Object.entries(outer.getAttributes()).forEach(([k, v]) => wrapper.setAttribute(k, v));
        wrapper.append(inner);
        return wrapper;
      }, node);
    }

    /*************************************************
     * After applyStyle, CKEditor often inserts
     * <strong><span style='color'>‚Ä¶</span></strong>
     * This lifts such inner spans so they wrap the inline tag.
     *************************************************/
    /**
     * CKEditor inserts   <strong>foo <span style='color'>bar</span></strong>
     * We want            <strong>foo <span style='color'><strong>bar</strong></span></strong>
     *
     * For every colour <span> that sits inside a non‚Äëspan inline tag,
     * clone that inline tag INSIDE the span (instead of pulling the span out).
     */
    function liftColorSpans(range) {
      const r = range.clone();
      r.enlarge(CKEDITOR.ENLARGE_INLINE);

      const walker = new CKEDITOR.dom.walker(r);
      walker.evaluator = n => n?.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');

      let span;
      while ((span = walker.next())) {
        const parent = span.getParent();
        if (!parent || parent.getName() === 'span') continue; // span already outer‚Äëmost or at root
        if (span.getFirst()?.getName && span.getFirst().getName() === parent.getName()) continue; // already cloned

        // 1. Clone the inline parent (e.g. <strong>)
        const clone = new CKEDITOR.dom.element(parent.getName());
        Object.entries(parent.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));

        // 2. Move span's children into the clone
        while (span.getFirst()) clone.append(span.getFirst().remove());

        // 3. Append the clone back into the span
        span.append(clone);
      }
    }

    /*************************************************
     * Remove only colour from selection, keep other inline styles
     *************************************************/
    function removeColor(range) {
      const w = new CKEDITOR.dom.walker(range.clone());
      w.evaluator = n => n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');
      let text;
      while ((text = w.next())) {
        const span = text.getAscendant('span', true);
        if (!span) continue;

        const start = text.equals(range.startContainer) ? range.startOffset : 0;
        const end = text.equals(range.endContainer) ? range.endOffset : text.getLength();
        const before = text.substring(0, start);
        const mid = text.substring(start, end);
        const after = text.substring(end);

        const chain = [];
        let p = text.getParent();
        while (p && !p.equals(span)) { chain.unshift(p); p = p.getParent(); }

        const build = (content, keepColor) => {
          if (!content) return null;
          const inside = wrapInside(new CKEDITOR.dom.text(content), chain);
          if (!keepColor) return inside;
          const coloured = new CKEDITOR.dom.element('span');
          coloured.setAttribute('style', span.getAttribute('style'));
          coloured.append(inside);
          return coloured;
        };

        [build(before, true), wrapInside(new CKEDITOR.dom.text(mid), chain), build(after, true)]
          .filter(Boolean)
          .forEach(f => span.insertBeforeMe(f));

        text.remove();
        if (!span.getChildCount()) span.remove();
      }
    }

    /******************* RichCombo UI ************************/
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        Object.entries(colors).forEach(([label, val]) => this.add(val, label, label));
      },
      onRender() { this.setValue(''); },

      onClick(value) {
        editor.focus();
        const sel = editor.getSelection();
        if (!sel) return;
        const ranges = sel.getRanges();
        editor.fire('lockSnapshot');

        if (value === 'default') {
          ranges.forEach(removeColor);
        } else {
          const style = new CKEDITOR.style({ element: 'span', styles: { color: value } });
          editor.applyStyle(style);
          ranges.forEach(liftColorSpans);
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
